.bit-grid {
	display: grid;
	margin: auto;
	text-align: center;
	font-size: small;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.bit-grid .bit-pos-marker {
	grid-area: 1 / 1 ;

	padding: 2px;
	font-style: italic;
	
	display: grid;
	grid-gap: 1px;
}

.bit-grid .byte-offset {
	grid-area: 2 / 2;

	padding: 2px;
	
	display: grid;
	grid-template-columns: 1fr;
	grid-gap: 1px;
}

.bit-grid .grid {
	grid-area: 2 / 1;

	border: 2px solid #428bca;
	background-color: #999999;
	font-size: 7pt;
	
	display: grid;
	grid-gap: 1px;
}

.bit-grid .field-fragment {
	cursor: pointer;
	background-color: #dfe3ee;
}

.bit-grid .field-fragment-rsvd {
	cursor: pointer;
	color: white;
	background-color: #c4c7bce5;
}

.bit-grid div.bit {
	background-color: #fff;
	cursor: pointer;
}

.bit-grid div.bit.highlight {
	background-color: #deff97;
}

.bit-grid .field-fragment {
	overflow: hidden;
}

.bit-grid .field-fragment-rsvd {
	overflow: hidden;
}
table.symbolic-names {
	table-layout: fixed;
	border-collapse: collapse;
	/*border: 3px solid purple;*/
}

table.symbolic-names th, table.symbolic-names td {
	padding: 5px;
	text-align: left;
}

#reset-btn {
	margin-right: 16px;
}

.field-editor {
	padding-bottom: 8px;
	margin-bottom: 24px;
}

#constant-left {
	width: 100px;
	text-align: center;
}

#constant-middle {
	background-color: #fff;
	width: 30px;
	border-left: 0;
	border-right: 0;
	pointer-events:none;
}

#constant-right {
	width: 100px;
	text-align: center;
	border-left-width: 0;
}

#constant-right:hover {
	border-left-width: 1px;
}

.constant-error {
	margin-left: 8px;
	line-height: 32px;
}
.title-container {
	display: flex;
	justify-content: space-between;
}

.title-container .address {
	color: rgba(0, 0, 0, 0.45);
	font-size: 14px;
	margin-left: 8px;
}

.with-hover-pointer:hover {
	cursor: pointer
}
.group-editor {
	margin-top: 20px;
	display: grid;
	grid-gap: 10px;
	grid-template-columns: 80px 200px;
}
.reg-list {
    height: 100%;
    padding: 16px;
    border-right: 1px solid rgb(240, 240, 240);
    display: flex;
    flex-direction: column;
}

.reg-list .tree-container {
    overflow: auto;
    height: 100%;
    margin-top: 8px;
}

.tree-title .act-edit {
    display: none;
    margin: 0 4px;
}

.tree-title:hover .act-edit {
    display: inline-block;
}

.tree-title:hover .act-edit:hover {
    color: red
}

.tree-title .act-delete {
    display: none;
    margin: 0 4px;
}

.tree-title:hover .act-delete {
    display: inline-block;
}

.tree-title:hover .act-delete:hover {
    color: red
}
.data-source-container {
	margin-top: 80px;
	display: flex;
	justify-content: center;
	align-content: center;
}

.data-source {
	width: 560px;
}

.data-picker-container {
	width: 100%;
}

.data-picker {
	font-size: 16px;
	color: rgba(0, 0, 0, 0.85);
  
	width: 100%;

	background-color: rgb(250, 250, 250);
	border: 1px dashed rgb(217, 217, 217);
	border-radius: 2px;
  
	text-align: center;
  
	display: inline-block;
	cursor: pointer;
}

.data-picker:hover {
	border-color: #40a9ff; 
}

.data-picker input {
	display: none;
}

.data-picker-icon {
	font-size: 3rem;
	color: #40a9ff;
	margin-bottom: 20px;
}
.app {
    width: 960px;
    margin-left: auto;
    margin-right: auto;
}

.app .content {
    padding: 1rem 0
}
html {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  color: #000;
  min-height: 100%;
}

#root {
  min-height: 100%;
}
